<template>
  <div class="box">
    <article class="media">
      <div class="media-left">
        <figure class="image is-64x64">
          <img src="https://bulma.io/images/placeholders/128x128.png" alt="Image">
        </figure>
      </div>
      <div class="media-content">
        <div class="content">
          <div class="level is-mobile info">
            <div class="level-left">
              <strong>John Smith</strong> <small>@johnsmith</small>
            </div>
            <div class="level-right">
              <small>2018-8-9</small>
            </div>
          </div>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean efficitur sit amet massa fringilla egestas. Nullam condimentum luctus turpis.</p>
        </div>
        <nav class="level is-mobile">
          <div class="level-left">
            <span class="tag">技术</span>
          </div>
          <div class="level-right">
            <div class="tip"><router-link to="/ArticleDetail/">MORE</router-link></div>
          </div>
        </nav>
      </div>
    </article>
  </div>
</template>

<script>
export default {
  name: 'ArticleItem',
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tip {
  color: #0087FF;
  cursor: pointer;
}
.info {
  margin-bottom: 0.5rem !important;
}
</style>



