}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  margin-right: 1em;
  padding-right: 2em;
  border-right: 1px solid #dbdbdb;
}
@media screen and (max-width: 760px) {
  .container {
    margin-right: 0;
    margin-bottom: 3em;
    padding-right: 0;
    border-right: none;
  }
}
</style>



